*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.body{
  width: 100%;
  height: 100%;
  background-color: #f0f0f3;
  display: flex;
  flex-flow: wrap row;
  justify-content: center;
  align-content: space-evenly;
}
.body nav{
  width: 100%;
}
.dhun-wrapper{
  width: 100%;
}
.dhun-wrapper .dhun-inner-wrapper{
  list-style-type: none;
  width: 100%;
  margin: auto;
  display: flex;
  flex-flow: wrap row;
  justify-content: space-around;
}
.dhun-inner-wrapper li{
  padding: 10px 0;
  text-align: center;
  margin: 15px 0;
  box-shadow: -5px -5px 20px #FFFFFF, 5px 5px 10px #aeaec0;
  border-radius: 12px;
}
.dhun-inner-wrapper .dhun{
  padding: 10px 50px;
  text-decoration: none;
  color: black;
  font-weight: 600;
  font-size: 16px;
  font-family:sans-serif;
}
.dhun-inner-wrapper .dhun:hover{
  color: red;
  transition: 0.3s;
  border-radius: 12px;
  box-shadow: inset -6px -6px 12px #FFFFFF, inset 6px 6px 12px #aeaec0;
}

@media screen and (max-width:565px){
  .dhun-inner-wrapper .dhun{
    font-size: 14px;
    padding: 9px 30px;
  }  
  .dhun-inner-wrapper li{
    padding: 9px 0;
  }
}

@media screen and (max-width:400px){
  .dhun-inner-wrapper .dhun{
    font-size: 13px;
    padding: 9px 18px;
  } 
  .dhun-wrapper .dhun-inner-wrapper{
    justify-content: space-evenly !important;
  } 
}

@media screen and (max-width:320px){
  .dhun-inner-wrapper .dhun{
    font-size: 12px;
    padding:7px 12px;
  }  
  .dhun-inner-wrapper li{
    padding: 7px 0px;
  }
}
@media screen and (max-width:280px){
  .dhun-inner-wrapper .dhun{
    font-size: 11px;
    padding: 5px 9px;
  }  
  .dhun-inner-wrapper li{
    padding: 5px 0px;
  }
}
@media screen and (max-width:240px){
  .dhun-inner-wrapper .dhun{
    padding: 5px 30px;
  }
  .dhun-inner-wrapper li{
    margin: 0px 0px 15px 0px !important;
  }
}