.content-wrapper{
    width: 100%;
}
.content-wrapper .content-inner-wrapper{
    min-width: 90%;
    width: 28% !important;
    box-shadow: -6px -6px 25px #FFFFFF, 6px 6px 15px #aeaec0;
    margin: auto;
    padding: 20px 20px 15px 20px;
}
.content-inner-wrapper button{
    margin: 15px auto 0px;
    display: block;
    font-size: 16px;
    padding: 7px 14px;
    border-radius: 8px;
    box-shadow: -5px -5px 16px #FFFFFF, 5px 5px 10px #aeaec0;
    border: 0;
    outline: 0;
}
.content-inner-wrapper button:hover{
    box-shadow: inset -5px -5px 10px #FFFFFF, inset 5px 5px 10px #aeaec0;
    transition: 0.3s;
    color: red;
}
.content-inner-wrapper img{
    width: 100%;
    box-shadow: inset -5px -5px 10px #FFFFFF, inset 5px 5px 10px #aeaec0;
    padding: 15px;
}
@media screen and (max-width:420px){
    .content-wrapper .content-inner-wrapper{
        width: 88% !important;
    }    
}
@media screen and (max-width:317px){
    .content-inner-wrapper button{
        font-size: 12px !important;
        margin: 10px auto 0px !important;
    }    
    .content-wrapper .content-inner-wrapper{
        width: 90% !important;
        min-width: 90% !important;
        padding: 15px 15px 10px 15px !important;
    }
}

@media screen and (max-width:240px){
    .content-wrapper .content-inner-wrapper{
        min-width: 90%;
    }    
}